.loudong {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background: #f0f4fb;
  padding: 80px 30px 60px 30px;
}
.loudong .tip {
  margin-top: 60px;
  margin-bottom: 20px;
}
.loudong .tip .title {
  width: 138px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  background: #ff9666;
  color: #fff;
  font-size: 14px;
  border-radius: 5px;
}
.loudong .main {
  position: relative;
  width: 100%;
  height: 710px;
  border-radius: 5px;
  background: #fff;
}
.loudong .main .left {
  float: left;
  width: 240px;
  height: 490px;
  background: #f7f8fb;
}
.loudong .main .left .left-item {
  width: 100%;
  height: 65px;
  line-height: 65px;
  text-align: center;
  color: #616b88;
  font-size: 16px;
}
.loudong .main .left .left-item img {
  vertical-align: middle;
}
.loudong .main .left .margin-left {
  box-sizing: border-box;
  margin-left: -15px;
}
.loudong .main .left .left-specil {
  width: 100%;
  height: 285px;
}
.loudong .main .right {
  box-sizing: border-box;
  position: absolute;
  left: 240px;
  top: 0;
  padding-left: 45px;
  padding-top: 20px;
}
.loudong .main .right .margin-top {
  margin-top: 58px;
  margin-bottom: 8px;
}
.loudong .main .right .mar {
  margin-bottom: 26px;
}
.loudong .main .right .line {
  height: 65px;
}
.loudong .main .right .line .wrapper {
  display: inline-block;
  width: 126px;
  height: 32px;
  line-height: 32px;
  background: #f6f9fd;
  border: 1px solid #d6dfe6;
  border-radius: 5px;
  color: #616b88;
  font-size: 14px;
  text-align: center;
  margin-right: 30px;
}
.loudong .main .bottom {
  position: absolute;
  left: 0;
  bottom: 20px;
  width: 100%;
  text-align: center;
}
.loudong .main .bottom .prev,
.loudong .main .bottom .next {
  display: inline-block;
  width: 130px;
  height: 36px;
  line-height: 36px;
  border-radius: 5px;
  color: #fff;
  text-align: center;
}
.loudong .main .bottom .prev {
  background: #ccc;
  margin-right: 250px;
}
.loudong .main .bottom .next {
  background: #46c1de;
}


