.success {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background: #f0f4fb;
  padding: 120px 30px 60px 30px;
}
.success .contain {
  position: relative;
  width: 1600px;
  height: 820px;
  box-sizing: border-box;
  padding: 26px 80px;
  background: #fff;
  color: #616b88;
  font-size: 16px;
  text-align: center;
}
.success .contain .pos {
  position: absolute;
  top: 36%;
  left: 40%;
}
.success .contain .pos .first {
  margin-bottom: 25px;
}
.success .contain .pos .second {
  margin-bottom: 60px;
}
.success .contain .pos .next {
  display: inline-block;
  width: 108px;
  height: 34px;
  line-height: 34px;
  background: #46c1de;
  border-radius: 10px;
  color: #fff;
  font-size: 16px;
}


