.house-message {
  position: fixed;
  top: 80px;
  right: 0;
  width: 600px;
  height: 1000px;
  background: #fff;
}
.house-message .title {
  color: #616b88;
  font-size: 16px;
  font-weight: 700;
  margin-left: 28px;
  margin-bottom: 18px;
}
.house-message .item {
  width: 380px;
  height: 35px;
  line-height: 35px;
  margin: 0 auto;
  padding-bottom: 8px;
}
.house-message .item .itemipt {
  float: right;
  width: 255px;
  height: 32px;
  background: #f6f9fd;
  border: 1px solid #d6dfe6;
  border-radius: 4px;
}
.house-message .buttons {
  width: 60%;
  margin: 0 auto;
  overflow: hidden;
}
.house-message .buttons .cancel {
  float: left;
  width: 125px;
  height: 35px;
  line-height: 35px;
  background: #d6dfe6;
  color: #616b88;
  font-size: 14px;
  margin-right: 100px;
}
.house-message .buttons .succed {
  float: left;
  width: 125px;
  height: 35px;
  line-height: 35px;
  background: #46c1de;
  color: #fff;
  font-size: 14px;
}


