.list {
  width: 1600px;
  height: 900px;
}
.list .top {
  overflow: hidden;
}
.list .top .portGrop {
  float: left;
  width: 550px;
  height: 50px;
}
.list .top .portGrop .export,
.list .top .portGrop .import {
  float: left;
  width: 130px;
  height: 36px;
  box-sizing: border-box;
  line-height: 36px;
  border: 1px solid #d6dfe6;
  border-radius: 5px;
  padding-left: 12px;
  margin-right: 24px;
  background: #f6f9fd;
}
.list .top .portGrop .export span,
.list .top .portGrop .import span {
  margin-right: 24px;
}
.list .top .portGrop .export img,
.list .top .portGrop .import img {
  vertical-align: top;
  margin-top: 10px;
}
.list .top .selGrop {
  float: left;
  width: 1050px;
  height: 50px;
}
.list .top .selGrop .selectStyl1 {
  width: 128px;
  height: 36px;
  border: 2px solid #d6dfe6;
  border-radius: 4px;
  margin-right: 12px;
}
.list .top .selGrop .selectStyl2 {
  width: 156px;
  height: 36px;
  border: 2px solid #d6dfe6;
  border-radius: 4px;
  margin-right: 12px;
}
.list .top .selGrop .search {
  width: 80px;
  height: 36px;
  background: #46c1de;
  color: #fff;
  font-size: 14px;
  border-radius: 4px;
}
.list .bottom {
  width: 1600px;
  height: 900px;
  box-sizing: border-box;
  margin-top: 15px;
  background: #fff;
  border-radius: 5px;
  padding: 35px 35px;
}
.list .bottom .status {
  overflow: hidden;
  margin-bottom: 25px;
}
.list .bottom .status li {
  float: left;
  margin-right: 42px;
  color: #616b88;
  font-size: 14px;
}
.list .bottom .status .icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}
.list .bottom .status .icon.green {
  background: #0ad27a;
}
.list .bottom .status .icon.red {
  background: #e47878;
}
.list .bottom .status .icon.yellow {
  background: #cbc341;
}
.list .bottom .status .icon.blue {
  background: #46c1de;
}
.list .bottom .status .icon.purpos {
  background: #b194ec;
}
.list .bottom .status .icon.grew {
  background: #c5c5c5;
}
.list .bottom .status .icon.deepgre {
  background: #8d8d8d;
}
.list .bottom .status .icon.black {
  background: #666;
}
.list .bottom table {
  width: 100%;
  border-radius: 5px;
}
.list .bottom table thead {
  width: 100%;
  background: #c9d9f8;
}
.list .bottom table thead th {
  width: 140px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  color: #616b88;
}
.list .bottom table tbody {
  width: 100%;
}
.list .bottom table tbody tr {
  background: #fff;
}
.list .bottom table tbody tr td {
  width: 140px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  color: #626873;
  font-size: 14px;
  border: 1px solid #d6dfe6;
}
.list .bottom table tbody tr td img {
  margin-right: 2px;
}
.list .bottom table tbody tr:nth-child(odd) {
  background: #f6f9fd;
}


