.louceng {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background: #f0f4fb;
  padding: 80px 30px 60px 30px;
}
.louceng .tip {
  margin-top: 60px;
  margin-bottom: 20px;
}
.louceng .tip .title {
  width: 138px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  background: #ff9666;
  color: #fff;
  font-size: 14px;
  border-radius: 5px;
}
.louceng .setting {
  width: 1600px;
  height: 210px;
  box-sizing: border-box;
  padding: 26px 80px;
  background: #fff;
  color: #616b88;
  font-size: 16px;
}
.louceng .setting .top {
  height: 50px;
  line-height: 50px;
}
.louceng .setting .top input {
  display: inline-block;
  height: 30px;
  background: #f6f9fd;
  border: 1px solid #d6dfe6;
  border-radius: 5px;
  padding-left: 10px;
}
.louceng .setting .top .loucen {
  display: inline-block;
  margin-right: 240px;
}
.louceng .setting .top .loucen input {
  width: 80px;
}
.louceng .setting .top .rules {
  display: inline-block;
  margin-right: 240px;
}
.louceng .setting .top .rules input {
  width: 100px;
}
.louceng .setting .top .danyuan {
  display: inline-block;
  margin-right: 85px;
}
.louceng .setting .top .danyuan input {
  width: 80px;
}
.louceng .setting .top .tips {
  display: inline-block;
}
.louceng .setting .top .tips .hint {
  color: #ff1722;
  font-size: 14px;
}
.louceng .setting .top .tips .hint-con {
  color: #616b88;
  font-size: 14px;
}
.louceng .setting .bottom {
  height: 130px;
  line-height: 130px;
}
.louceng .setting .bottom .hushu {
  position: relative;
}
.louceng .setting .bottom .hushu .left {
  float: left;
  width: 10%;
}
.louceng .setting .bottom .hushu table {
  float: left;
  width: 90%;
  height: 90px;
  margin-top: 20px;
}
.louceng .setting .bottom .hushu table tr,
.louceng .setting .bottom .hushu table th {
  height: 45px;
  line-height: 45px;
  background: #f6f9fd;
  color: #616b88;
  font-size: 14px;
  border: 1px solid #d6dfe6;
}
.louceng .setting .bottom .hushu table tr input,
.louceng .setting .bottom .hushu table th input {
  width: 60px;
  height: 24px;
  border: 1px solid #d6dfe6;
  border-radius: 5px;
  padding-left: 10px;
  color: #616b88;
}
.louceng .content {
  position: relative;
  width: 1600px;
  height: 900px;
  box-sizing: border-box;
  margin-top: 30px;
  background: #fff;
  color: #616b88;
  font-size: 16px;
  padding: 60px 50px 130px 50px;
}
.louceng .content table {
  width: 100%;
}
.louceng .content table thead {
  width: 100%;
}
.louceng .content table thead tr {
  width: 100%;
}
.louceng .content table thead tr th {
  width: 428px;
}
.louceng .content table tr,
.louceng .content table th,
.louceng .content table td {
  height: 45px;
  line-height: 45px;
  color: #616b88;
  font-size: 14px;
  border: 1px solid #d6dfe6;
  text-align: center;
}
.louceng .content table tr,
.louceng .content table th {
  background: #f6f9fd;
}
.louceng .content table tbody tr:nth-child(odd) {
  background: #fff;
}
.louceng .content .bottom {
  position: absolute;
  left: 0;
  bottom: 40px;
  width: 100%;
  text-align: center;
}
.louceng .content .bottom .prev,
.louceng .content .bottom .next {
  display: inline-block;
  width: 130px;
  height: 36px;
  line-height: 36px;
  border-radius: 5px;
  color: #fff;
  text-align: center;
}
.louceng .content .bottom .prev {
  background: #ccc;
  margin-right: 250px;
}
.louceng .content .bottom .next {
  background: #46c1de;
}


