.house-view {
  padding-left: 20px;
}
.house-view .tip {
  margin-top: 25px;
  margin-bottom: 20px;
}
.house-view .tip .title {
  width: 138px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  background: #ff9666;
  color: #fff;
  font-size: 14px;
  border-radius: 5px;
}
.house-view .dothing {
  clear: both;
  overflow: hidden;
  margin-bottom: 25px;
}
.house-view .dothing .item {
  float: left;
  box-sizing: border-box;
  width: 125px;
  height: 35px;
  line-height: 35px;
  background: #f6f9fd;
  color: #616b88;
  border: 1px solid #d6dfe6;
  border-radius: 4px;
  font-size: 14px;
  padding-left: 10px;
  margin-right: 16px;
  vertical-align: middle;
}
.house-view .dothing .item span {
  margin-right: 30px;
}
.house-view .dothing .item img {
  vertical-align: middle;
}
.house-view .dothing .item1 {
  float: left;
  box-sizing: border-box;
  width: 125px;
  height: 35px;
  line-height: 35px;
  background: #46c1de;
  color: #fff;
  border-radius: 4px;
  font-size: 14px;
  padding-left: 10px;
  vertical-align: middle;
}
.house-view .dothing .item1 span {
  margin-right: 30px;
}
.house-view .dothing .item1 img {
  vertical-align: middle;
}
.house-view .status {
  overflow: hidden;
  margin-bottom: 25px;
}
.house-view .status li {
  float: left;
  margin-right: 42px;
}
.house-view .status li .icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}
.house-view .status li .icon.green {
  background: #0ad27a;
}
.house-view .status li .icon.red {
  background: #e47878;
}
.house-view .status li .icon.yellow {
  background: #cbc341;
}
.house-view .status li .icon.blue {
  background: #46c1de;
}
.house-view .status li .icon.purpos {
  background: #b194ec;
}
.house-view .status li .icon.grew {
  background: #c5c5c5;
}
.house-view .status li .icon.deepgre {
  background: #8d8d8d;
}
.house-view .status li .icon.black {
  background: #666;
}
.house-view table {
  width: 100%;
}
.house-view table .item.active {
  background: #46c1de;
  color: #fff;
}
.house-view table thead {
  width: 100%;
}
.house-view table thead tr {
  width: 100%;
}
.house-view table thead tr th {
  width: 428px;
}
.house-view table tr,
.house-view table th,
.house-view table td {
  height: 45px;
  line-height: 45px;
  color: #616b88;
  font-size: 14px;
  border: 1px solid #d6dfe6;
  text-align: center;
}
.house-view table tr,
.house-view table th {
  background: #f6f9fd;
}
.house-view table tbody tr:nth-child(odd) {
  background: #fff;
}


