@import "common/stylus/style.css";
.nav-wrapper {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 100%;
}
.nav-wrapper .left-wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 255px;
          flex: 0 0 255px;
  z-index: 30;
  background: #616b88;
}
.nav-wrapper .left-wrapper .logo {
  height: 80px;
  background: #565e78;
  vertical-align: middle;
  text-align: center;
}
.nav-wrapper .left-wrapper .logo img {
  display: inline-block;
  margin-top: 17px;
  width: 118px;
  height: 46px;
}
.nav-wrapper .left-wrapper .title {
  height: 50px;
  background: #616b88;
  border-bottom: 1px solid #69738e;
}
.nav-wrapper .left-wrapper .title img {
  margin: 14px 10px 0px 30px;
  vertical-align: top;
  display: inline-block;
}
.nav-wrapper .left-wrapper .title h1 {
  line-height: 50px;
  display: inline-block;
  font-size: 18px;
  color: #eaeaea;
}
.nav-wrapper .left-wrapper .tab {
  background: #616b88;
}
.nav-wrapper .left-wrapper .tab .li-item {
  position: relative;
  height: 50px;
  line-height: 50px;
  border-bottom: 2px solid #69738e;
}
.nav-wrapper .left-wrapper .tab .li-item i :hover {
  background: transparent;
  border-left: none;
}
.nav-wrapper .left-wrapper .tab .li-item .icon-箭头 {
  display: inline-block;
  position: absolute;
  top: 16px;
  right: 12px;
}
.nav-wrapper .left-wrapper .tab .li-item .icon-箭头 :hover {
  background: transparent;
  border-left: none;
}
.nav-wrapper .left-wrapper .tab .li-item :hover {
  background: #47506c;
  color: #46c1de;
  border-left: 4px solid #46c1de;
}
.nav-wrapper .left-wrapper .tab .li-item a {
  padding-left: 30px;
  display: block;
  color: #fff;
  font-size: 14px;
}
.nav-wrapper .left-wrapper .tab .li-item a.item-selected {
  color: #9d8ad3;
  background: #47506c;
}
.nav-wrapper .left-wrapper .tab .li-item a.active {
  background: #fff;
  color: #46c1de;
  border-left: 4px solid #46c1de;
}
.nav-wrapper .header-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 20;
  padding-left: 255px;
  height: 80px;
  line-height: 80px;
  background: #fff;
}
.nav-wrapper .header-wrapper .notice {
  float: left;
  height: 38px;
  line-height: 38px;
  background: #fdf7f4;
  color: #ff915b;
  font-size: 14px;
  width: 60%;
  margin-left: 14px;
  margin-top: 22px;
  border-radius: 10px;
  padding-left: 28px;
}
.nav-wrapper .header-wrapper .notice .rightimg {
  float: right;
  vertical-align: top;
  margin-top: 14px;
  margin-right: 5px;
}
.nav-wrapper .header-wrapper .right {
  float: left;
  margin-right: 35px;
  padding-left: 15%;
  font-size: 14px;
  color: #a9b3c7;
}
.nav-wrapper .header-wrapper .right .head {
  vertical-align: top;
  margin-top: 24px;
}
.nav-wrapper .header-wrapper .right .name {
  margin-right: 4px;
}
.nav-wrapper .header-wrapper .tabs {
  position: relative;
  float: left;
  width: 100%;
  height: 180px;
  background: #f0f4fb;
}
.nav-wrapper .header-wrapper .tabs .nav-wrap {
  height: 50px;
  line-height: 50px;
  padding-left: 30px;
}
.nav-wrapper .header-wrapper .tabs .nav-wrap .item-selected {
  color: #46c1de;
  font-size: 14px;
}


